






import Vue from "vue";
import Component from "vue-class-component";
@Component
export default class Phone extends Vue {
  get curView() {
    const origin = 'https://mywentu.github.io';
    const page =
      this.$route.name === "docBase"
        ? `${origin}/#/ruicss/case/overview`
        : `${origin}/#/ruicss/case/${this.$route.path.split("/").pop()}`;
    return page;
  }
}
